}

.jumbotron-caption {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.nav-link:hover {
  text-decoration: underline;
}

.padding {
  padding-bottom: 2rem;
}

.fa-envelope:hover,
.fa-linkedin-in:hover {
  color: rgb(82, 83, 81) !important;
}

